
















import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";
import { Generator } from "more-entropy";
import { randomBytes } from "crypto";
import Spinner from "vue-simple-spinner";
import { walletFromEntropy } from "@/crypto"; // Adjust this to handle 256-bit entropy for a 24-word mnemonic

@Component({
  components: {
    Spinner,
  },
})
export default class WalletFromEntropy extends Vue {
  public entropy: number[] = [];
  public entropyCurrent: number | null = null;
  public entropyProgress: Record<string, string | number> | null = {};
  public entropyTimer: NodeJS.Timeout | null = null;

  public mounted(): void {
    this.entropyTimer = setInterval(this.generateEntropyProgress, 100);

    // Use 256 bytes for 24-word mnemonic generation
    new Generator().generate(2048, values => {
      // Generate a 256-bit entropy for a 24-word mnemonic
      this.entropy = values.concat(Array.from(randomBytes(256 / 8))); // 32 bytes

      this.generateWallet();
    });
  }

  public generateWallet(): void {
    try {
      const mnemonicEntropy = this.shuffle(this.entropy).slice(0, 32); // 32 bytes for 256-bit entropy
      this.$router.push({
        name: "wallet",
        params: { wallet: JSON.stringify(walletFromEntropy(mnemonicEntropy)) },
      });
    } catch (error) {
      // Handle invalid passphrase or other error
    } finally {
      clearInterval(this.entropyTimer);
    }
  }

  private shuffle(items: number[]): number[] {
    for (let i = items.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [items[i], items[j]] = [items[j], items[i]];
    }
    return items;
  }

  private generateEntropyProgress(): void {
    let value: string = this.numberBetween(1, 100).toString();

    while (value.length < 3) {
      value = "0" + value;
    }

    this.entropyProgress = {
      title: randomBytes(20)
        .toString("hex")
        .substring(0, 1)
        .toUpperCase(),
      subtitle: value,
    };
  }

  private numberBetween(min: number, max: number): number {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }
}
